<template>
  <div class="vault" style="margin: 20px;">
      <el-row :gutter="24">
        <!--   1. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>1</span>
            </div>
            <el-form :model="proposal" label-position="left" label-width="80px">
              <el-form-item label="">
                <el-input v-model="proposal.dst" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="proposal.details" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="createProposal()"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
        <!--   2. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card class="box-card">
            <div slot="header" class="clearfix">
              <span>2</span>
            </div>
            <el-form :model="voteProposalInfo" label-width="80px" >
              <el-form-item label="ID">
                <el-input v-model="voteProposalInfo.id" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-select v-model="voteProposalInfo.auth" placeholder="" style="width: 100%">
                  <el-option label="" :value="true"></el-option>
                  <el-option label="" :value="false"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="voteProposal()"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
        <!--   3. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>3</span>
            </div>
            <el-form  label-width="110px" >
              <el-form-item label="ID">
                <el-input v-model="voteProposalInfo.id" placeholder="ID"></el-input>
              </el-form-item>
              <el-form-item label="" style="height: 40px" >
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="proposals()"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
<!--      </el-row>-->
<!--      <el-row :gutter="24">-->
        <!--   4. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>4</span>
<!--              <el-button style="float: right; padding: 3px 0" @click="dialogCreateVisible=true" type="primary"></el-button>-->
            </div>
            <el-form  label-width="110px" >
              <el-form-item label="" style="height: 101px" >
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="dialogCreateVisible=true" ></el-button>
              </el-form-item>
            </el-form>
          </el-card>
          <el-dialog
              title=""
              :visible.sync="dialogCreateVisible"
              center
              :before-close="handleClose">
            <el-form :model="validatorInfo" label-width="80px" >
              <el-form-item label="">
                <el-input v-model="validatorInfo.feeAddr" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="validatorInfo.moniker" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="validatorInfo.identity" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="validatorInfo.website" placeholder="" value="https://www.bhpnet.io"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="validatorInfo.email" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="validatorInfo.details" placeholder="" value=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="createValidator()"></el-button>
              </el-form-item>
            </el-form>
          </el-dialog>
        </el-col>
        <!--   5.BHP32BHP -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>5BHP32BHP</span>
            </div>
            <el-form :model="stake" label-width="110px" >
              <el-form-item label="">
                <el-input v-model="address" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="stake.money" placeholder="">
                  <template slot="append">BHP</template>
                </el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="stakeValidator()"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
        <!--   6. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>6</span>
            </div>
            <el-form  label-width="110px" >
              <el-form-item label="">
                <el-input v-model="address" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="userAddress" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="getStakeInfo()"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
        <!--   7. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>7</span>
              <el-button style="float: right; padding: 3px 0" @click="getActiveValidators()" type="success"></el-button>
            </div>
          </el-card>
        </el-col>
        <!--   8.TOP -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>8</span>
              <el-button style="float: right; padding: 3px 0" @click="getTopValidators()" type="success"></el-button>
            </div>
          </el-card>
        </el-col>
        <!--   9. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>9</span>
              <el-button style="float: right; padding: 3px 0" @click="totalStake()" type="success"></el-button>
            </div>
          </el-card>
        </el-col>
        <!--   11. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>11.11</span>
              <el-tooltip class="item" effect="dark" content="BHP" placement="top-start">
                <el-button style="float: right; padding: 3px 0" type="danger"  ></el-button>
              </el-tooltip>
            </div>
            <el-form  label-width="110px" >
              <el-form-item label="">
                <el-input v-model="address" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="withdrawProfits()"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
        <!--   12. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>12.</span>
              <el-tooltip class="item" effect="dark" content="" placement="top-start">
                <el-button style="float: right; padding: 3px 0" type="danger"  ></el-button>
              </el-tooltip>
            </div>
            <el-form  label-width="110px" >
              <el-form-item label="">
                <el-input v-model="address" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="unstake()"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
        <!--   13. -->
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
          <el-card>
            <div slot="header" class="clearfix">
              <span>13.(3)</span>
              <el-tooltip class="item" effect="dark" content="3" placement="top-start">
                <el-button style="float: right; padding: 3px 0" type="danger"  ></el-button>
              </el-tooltip>
            </div>
            <el-form  label-width="110px" >
              <el-form-item label="">
                <el-input v-model="address" placeholder=""></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="withdrawStaking()"></el-button>
              </el-form-item>
            </el-form>
          </el-card>
        </el-col>
                <!--   10. -->
                <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
                  <el-card>
                    <div slot="header" class="clearfix">
                      <span>10</span>
                    </div>
                    <el-form  label-width="110px" >
                      <el-form-item label="">
                        <el-input v-model="address" placeholder=""></el-input>
                      </el-form-item>
                      <el-form-item>
                        <el-button type="primary" @click="getValidatorInfo()"></el-button>
                      </el-form-item>
                    </el-form>
                  </el-card>
                </el-col>
        <el-col :span="12" style="margin:10px auto auto;" :xs="24" :sm="24" :md="24" :lg="12" :xl="8">
                  <el-card>
                    <div slot="header" class="clearfix">
                      <span></span>
                    </div>
                    <el-form  label-width="110px" >
                        <el-form-item label="Remix">
                            <el-link href="http://remix.ethereum.org/" target="_blank">http://remix.ethereum.org/</el-link>
                        </el-form-item>
                        <el-form-item label="">
                            <el-link href="https://docs.bhpnet.io/" target="_blank">https://docs.bhpnet.io/</el-link>
                        </el-form-item>
                        <el-form-item label="">
                          <el-link href="https://testnet.bhpnet.io/" target="_blank">https://testnet.bhpnet.io/</el-link>
                        </el-form-item>
                        <el-form-item label="">
                            <el-link href="https://github.com/bhpnet/bhp-system-contracts" target="_blank">https://github.com/bhpnet/bhp-system-contracts</el-link>
                        </el-form-item>
                        <el-form-item label="">
                            0x000000000000000000000000000000000000f000
                        </el-form-item>
                        <el-form-item label="">
                            0x000000000000000000000000000000000000f001
                        </el-form-item>
                        <el-form-item label="">
                            0x000000000000000000000000000000000000f002
                        </el-form-item>
                    </el-form>
                  </el-card>
         </el-col>
      </el-row>
      <!--    -->
      <el-dialog
          :title="title"
          :visible.sync="dialogTableVisible"
          :width="dialogWidth"
      >
        <el-table :data="tableData">
          <el-table-column
              label=""
              type="index"
              align="center"
              width="100">
          </el-table-column>
          <el-table-column
              prop="address"
              align="center"
              label=""
              >
          </el-table-column>
        </el-table>
      </el-dialog>
      <!--    -->
      <el-dialog
          :title="title"
          :visible.sync="dialogResultVisible"
          :width="dialogWidth"
          center
          :before-close="handleClose">
          <span>{{ info }}</span>
          <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogResultVisible = false"></el-button>
        </span>
      </el-dialog>
      <!--    -->
      <el-dialog
        title=""
        :visible.sync="dialogValidatorVisible"
        :width="dialogWidth"
        center
        :before-close="handleClose">
        <el-form :model="getValidatorInfoResult" label-width="120px" label-position="left" >
          <el-form-item  label="">
            <el-input v-model="getValidatorInfoResult[0]" ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-bind:value="getValidatorInfoResult[2] | toBHP" >
              <template slot="append">BHP</template>
            </el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-bind:value="getValidatorInfoResult[3] | toBHP" >
              <template slot="append">BHP</template>
            </el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-bind:value="getValidatorInfoResult[4] | toBHP"  >
              <template slot="append">BHP</template>
            </el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getValidatorInfoResult[5]"  ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getValidatorInfoResult[6]"  ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-select  placeholder="" style="width: 100%;" value="">
              <el-option
                  v-for="item in getValidatorInfoResult[7]"
                  :key="item"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
    </el-dialog>
      <!--    -->
      <el-dialog
          title=""
          :visible.sync="dialogStakeVisible"
          :width="dialogWidth"
          :before-close="handleClose">
        <el-form :model="getValidatorInfoResult" label-width="120px" label-position="left" >
          <el-form-item  label="">
            <el-input v-bind:value="getValidatorInfoResult[0] | toBHP" >
              <template slot="append">BHP</template>
            </el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getValidatorInfoResult[1]"  :disabled="true" ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getValidatorInfoResult[2]"  :disabled="true" ></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="dialogStakeVisible = false"></el-button>
          </span>
      </el-dialog>
      <!--    3.-->
      <el-dialog
          :title="title"
          :visible.sync="dialogProposalsVisible"
          :width="dialogWidth"
          center
          :before-close="handleClose">
        <el-form :model="getProposalsInfoResult" label-width="120px" label-position="left" >
          <el-form-item  label="">
            <el-input v-model="getProposalsInfoResult[0]"  :disabled="true"></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getProposalsInfoResult[1]" ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getProposalsInfoResult[2]" ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-bind:value="getProposalsInfoResult[3] | formatTime" ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getProposalsInfoResult[4]"  :disabled="true" ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getProposalsInfoResult[5]"  :disabled="true" ></el-input>
          </el-form-item>
          <el-form-item  label="">
            <el-input v-model="getProposalsInfoResult[6]"  :disabled="true" ></el-input>
          </el-form-item>
        </el-form>
      </el-dialog>
  </div>
</template>

<script>
import {
  createProposal,
  voteProposal,
  getActiveValidators,
  getTopValidators,
  createOrEditValidator,
  stake,
  getValidatorInfo,
  getStakingInfo,
  proposals,
  withdrawProfits,
  withdrawStaking,
  unstake,
  totalStake
} from "@/web3/bpos";
import {bhp} from "@/configure/conf";
import BigNumber from "bignumber.js";
export default {
  data() {
    return {
      dialogTableVisible: false,
      dialogResultVisible: false,
      dialogStakeVisible:false,
      dialogValidatorVisible:false,
      dialogProposalsVisible:false,
      dialogCreateVisible:false,
      dialogWidth: 0,
      title:'',
      address:'',
      userAddress:'',
      info:'',
      proposal:{
        dst:"",
        details:""
      },
      voteProposalInfo:{
        id:'',
        auth:''
      },
      tableData:[],
      getTopValidatorsTableData:[],
      getProposalsInfoResult:[],
      validatorInfo:{
        feeAddr:'',
        moniker:'',
        identity:'',
        website:'',
        email:'',
        details:''
      },
      stake:{
        validator:'',
        //
        money:0
      },
      formInline: {
        user: '',
        region: ''
      },
      getValidatorInfoResult:[]
    };
  },
  beforeDestroy() {
    clearInterval(this.timer);
  },
  filters:{
    toBHP:(amount)=>{
        let balance = new BigNumber(amount)
        return balance.div("1000000000000000000").toPrecision(18)
    },
    formatTime:(time)=>{
      return new Date(parseInt(time) * 1000).toLocaleString();
    }
  },
  created() {
    if (this.$store.state.wallet.connected) {
    }
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        this.setDialogWidth()
      })()
    }
  },
  methods: {
    handleClose() {
      this.dialogTableVisible= false
      this.dialogResultVisible=false
      this.dialogStakeVisible=false
      this.dialogValidatorVisible=false
      this.dialogProposalsVisible=false
      this.dialogCreateVisible=false
    },
    getErrorInfo(result){
      this.title=""
      this.info=result
      this.dialogResultVisible = true
    },
    verifyConnect(){
      let wallet = this.$store.state.wallet;
      if (!wallet.connected) {
        alert("");
        return
      }
    },
    createProposal(){
      this.verifyConnect()
      // 
      this.$parent.loading = true;
      this.$parent.flag1 = true;
      this.$parent.flag2 = false;
      this.$parent.flag3 = false;
      createProposal(
          this.$store.state.wallet,
          this.proposal.dst,
          this.proposal.details,
          data => {
            console.log(data);
            this.$parent.url = bhp + data.message;
            this.$parent.flag2 = true;
            this.$parent.flag1 = false;
            this.$parent.flag3 = false;
          }
      ).then(res => {
        this.$parent.loading = false;
        //ID
        let result=res.events.LogCreateProposal.returnValues[0];
        this.voteProposalInfo.id=result
        this.title=""
        this.info="ID"+result
        this.dialogResultVisible = true
      }).catch(err=>{
        this.$parent.loading = false;
        this.getErrorInfo(err)
      })
    },
    voteProposal(){
      this.verifyConnect()
      // 
      this.$parent.loading = true;
      this.$parent.flag1 = true;
      this.$parent.flag2 = false;
      this.$parent.flag3 = false;
      voteProposal(
          this.$store.state.wallet,
          this.voteProposalInfo.id,
          this.voteProposalInfo.auth,
          data => {
            console.log(data);
            this.$parent.url = bhp + data.message;
            this.$parent.flag2 = true;
            this.$parent.flag1 = false;
            this.$parent.flag3 = false;
          }
      ).then(res => {
        this.$parent.loading = false;
        //ID
        console.log(JSON.stringify(res))
        let result=res.events.LogVote.returnValues.auth;
        this.$alert(""+result, 'ID', {
          confirmButtonText: '',
          callback: () => {
          }
        });

      }).catch(err=>{
        this.$parent.loading = false;
        this.getErrorInfo(err)
      })
    },
    proposals(){
      this.verifyConnect()
      this.getProposalsInfoResult=[]
      proposals(this.$store.state.wallet,this.voteProposalInfo.id).then(res => {
        console.log(JSON.stringify(res))
        this.getProposalsInfoResult=res
        this.title=""
        this.dialogProposalsVisible=true
      })
    },
    createValidator(){
      this.verifyConnect()
      this.dialogCreateVisible=false
      this.$parent.loading = true;
      this.$parent.flag1 = true;
      this.$parent.flag2 = false;
      this.$parent.flag3 = false;
      createOrEditValidator(
          this.$store.state.wallet,
          this.validatorInfo.feeAddr,
          this.validatorInfo.moniker,
          this.validatorInfo.identity,
          this.validatorInfo.website,
          this.validatorInfo.email,
          this.validatorInfo.details,
          data => {
            this.$parent.url = bhp + data.message;
            this.$parent.flag2 = true;
            this.$parent.flag1 = false;
            this.$parent.flag3 = false;
          }
      ).then(res => {
        console.log(JSON.stringify(res))
        this.$parent.loading = false;
        //ID
        let result=res.status;
        this.$alert(result, '', {
          confirmButtonText: '',
        });
      }).catch(err=>{
        this.dialogCreateVisible=false
        this.$parent.loading = false;
        this.getErrorInfo(err)
      })
    },
    stakeValidator(){
      this.verifyConnect()

      // 
      this.$parent.loading = true;
      this.$parent.flag1 = true;
      this.$parent.flag2 = false;
      this.$parent.flag3 = false;

      stake(
          this.$store.state.wallet,
          this.address,
          this.stake.money.toString(),
          data => {
            console.log(data);
            this.$parent.url = bhp + data.message;
            this.$parent.flag2 = true;
            this.$parent.flag1 = false;
            this.$parent.flag3 = false;
          }
      ).then(res => {
        console.log(JSON.stringify(res))
        this.$parent.loading = false;
        let result=res.events;
        this.title=""
        this.info=result
        this.dialogResultVisible = true
      }).catch(err=>{
        this.$parent.loading = false;
        this.getErrorInfo(err)
      })
    },
    getTopValidators(){
      this.verifyConnect()
      getTopValidators(this.$store.state.wallet).then(res => {
        this.tableData=[]
        res.forEach(address => {
          let data={
            address:address
          }
          console.log(address)
          this.tableData.push(data)
        })
        this.title=""
        this.dialogTableVisible=true
      })
    },
    getActiveValidators(){
      this.verifyConnect()
      getActiveValidators(this.$store.state.wallet).then(res => {
        this.tableData=[]
        res.forEach(address => {
          let data={
            address:address
          }
          console.log(address)
          this.tableData.push(data)
        })
        this.title=""
        this.dialogTableVisible=true
      })
    },
    getValidatorInfo(){
      this.verifyConnect()
      this.getValidatorInfoResult=[]
      getValidatorInfo(this.$store.state.wallet,this.address).then(res => {
        console.log(res)
        this.getValidatorInfoResult=res
        this.title=""
        this.dialogValidatorVisible=true
      })
    },
    getStakeInfo(){
      this.verifyConnect()
      this.getValidatorInfoResult=[]
      getStakingInfo(this.$store.state.wallet,this.address,this.userAddress).then(res => {
        console.log(JSON.stringify(res))
        this.getValidatorInfoResult=res
        this.title=""
        this.dialogStakeVisible=true
      })
    },
    totalStake(){
      this.verifyConnect()
      totalStake(this.$store.state.wallet).then(res => {
        console.log(JSON.stringify(res))
        let balance = new BigNumber(res)
        this.info=""+balance.div("1000000000000000000").toPrecision(18)+" BHP"
        this.title=""
        this.dialogResultVisible=true
      })
    },
    withdrawProfits(){
      this.verifyConnect()
      // 
      this.$parent.loading = true;
      this.$parent.flag1 = true;
      this.$parent.flag2 = false;
      this.$parent.flag3 = false;
      withdrawProfits(
          this.$store.state.wallet,
          this.address,
          data => {
            this.$parent.url = bhp + data.message;
            this.$parent.flag2 = true;
            this.$parent.flag1 = false;
            this.$parent.flag3 = false;
          }
      ).then(res => {
        this.$parent.loading = false;
        console.log(JSON.stringify(res))
        this.title=""
        this.info=res
        this.dialogResultVisible = true

      }).catch(err=>{
        this.$parent.loading = false;
        this.getErrorInfo(err)
      })
    },
    withdrawStaking(){
      this.verifyConnect()
      // 
      this.$parent.loading = true;
      this.$parent.flag1 = true;
      this.$parent.flag2 = false;
      this.$parent.flag3 = false;
      withdrawStaking(
          this.$store.state.wallet,
          this.address,
          data => {
            this.$parent.url = bhp + data.message;
            this.$parent.flag2 = true;
            this.$parent.flag1 = false;
            this.$parent.flag3 = false;
          }
      ).then(res => {
        this.$parent.loading = false;
        console.log(JSON.stringify(res))
        this.title=""
        this.info=res
        this.dialogResultVisible = true

      }).catch(err=>{
        this.$parent.loading = false;
        this.getErrorInfo(err)
      })
    },
    unstake(){
      this.verifyConnect()
      // 
      this.$parent.loading = true;
      this.$parent.flag1 = true;
      this.$parent.flag2 = false;
      this.$parent.flag3 = false;
      unstake(
          this.$store.state.wallet,
          this.address,
          data => {
            this.$parent.url = bhp + data.message;
            this.$parent.flag2 = true;
            this.$parent.flag1 = false;
            this.$parent.flag3 = false;
          }
      ).then(res => {
        this.$parent.loading = false;
        console.log(JSON.stringify(res))
        this.title=""
        this.info=res
        this.dialogResultVisible = true

      }).catch(err=>{
        this.$parent.loading = false;
        this.getErrorInfo(err)
      })
    },
    setDialogWidth() {
      let val = document.body.clientWidth;
      const def = 900 // 
      if (val < def) {
        this.dialogWidth = '90%'
      } else {
        this.dialogWidth = def + 'px'
      }
    },
  },
};
</script>

